import React, { Component } from 'react';
class SideMenuItem extends Component {
	render() {
		return (
			<div className="SMI_con">
				<div className="SMI_decade">{this.props.item.decade}</div>
				<div className="SMI_decade">
					<img className="sideMenu_icon" src="/images/icons/down" />
				</div>
			</div>
		);
	}
}
export default class SideMenu extends Component {
	constructor(props) {
		super(props);
	}
	render() {
		const triggeredClass = `sideMenu ${this.props.open ? 'sideMenuOpen' : ''}`;
		return (
			<div className={triggeredClass}>
				<div className="sideMenu-con">
					<div className="sideMenu_row sideMenu_X">xd</div>
					{this.props.NavList.map((item) => (
						<div className="sideMenu_row">
							<SideMenuItem item={item} />
						</div>
					))}
				</div>
			</div>
		);
	}
}
